/* === Slot Booking Wrapper (Glass Effect) === */
.slot-booking-wrapper {
  padding: 20px;
  font-family: Arial, sans-serif;
  text-align: center;
  margin-top: 60px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  gap: 20px;

  /* Glass effect */
  background: rgba(255, 255, 255, 0.15);
  /* backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px); */
  border-radius: 15px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.25);
  color: #111; /* dark text for readability */
  transition: all 0.3s ease;
}
h2{
  color: #004c99;
}
/* === Booking Section === */
.booking-section {
  background: rgba(255, 255, 255, 0.15); /* semi-transparent glass */
  /* backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px); */
  padding: 20px 25px;
  border-radius: 15px;
  width: 70%;
  max-width: 800px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  font-size: 16px;
  line-height: 1.5;
  color: #111;
  transition: all 0.3s ease;
}

/* Section Headings */
.section-heading {
  font-size: 22px;
  font-weight: bold;
  color: #004c99;
  width: 100%;
  text-align: left;
  border-left: 5px solid #007bff;
  padding-left: 10px;
  margin-bottom: 10px;
  line-height: 1.3;
  text-shadow: 0 1px 2px rgba(255,255,255,0.3);
}

/* === Slot Grid === */
.slot-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  gap: 15px;
  width: 100%;
  justify-items: center;
}

.slot-box {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.3s ease;
  color: white;
  backdrop-filter: blur(5px);
}

/* Slot colors */
.slot-box.available { background: #5ed87a; border: 2px solid #02a302; }
.slot-box.occupied { background: #f37b7b; border: 2px solid #fa0000; cursor: not-allowed; }
.slot-box.reserved { background: #ffc107; color: #333; cursor: not-allowed; }
.slot-box.special { background: #17a2b8; cursor: not-allowed; }
.slot-box.active { border: 3px solid #007bff; }

/* Hover effects */
.slot-box.available:hover { background: #00da33; }
.slot-box.occupied:hover { background: #c82333; }
.slot-box.reserved:hover { background: #e0a800; }
.slot-box.special:hover { background: #138496; }

/* === Booking Form === */
.booking-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
}

.booking-form input {
  padding: 12px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 16px;
  width: 100%;
  backdrop-filter: blur(5px);
}

.booking-form button {
  padding: 12px;
  border-radius: 8px;
  border: none;
  background-color: #007bff;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.booking-form button:hover { background-color: #0056b3; transform: scale(1.05); }

/* Selected slot info */
.selected-info {
  font-weight: bold;
  color: #007bff;
  margin-top: 5px;
}

/* === Legend === */
.slot-legend {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  gap: 8px;
}

.legend-badge {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px 10px;
  border-radius: 6px;
  font-size: 14px;
  color: white;
  font-weight: bold;
  min-width: 70px;
  min-height: 30px;
  backdrop-filter: blur(5px);
}

.legend-badge.available { background: #28a745; }
.legend-badge.occupied { background: #dc3545; }
.legend-badge.reserved { background: #ffc107; color: #333; }
.legend-badge.special { background: #17a2b8; }

/* ================= RESPONSIVE ================= */

/* üì± Small phones */
@media (max-width: 480px) {
  .booking-section {
    width: 100%;
    border-radius: 12px;
    padding: 15px;
    font-size: 14px;
  }

  .section-heading {
    font-size: 18px;
    text-align: center;
  }

  .slot-grid {
    gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(60px, 1fr));
  }

  .slot-box {
    width: 60px;
    height: 60px;
    font-size: 14px;
  }

  .booking-form input,
  .booking-form button {
    font-size: 14px;
  }

  .slot-legend {
    justify-content: center;
    gap: 6px;
  }
  .legend-badge {
    font-size: 12px;
    padding: 5px 8px;
    min-width: 60px;
    min-height: 28px;
  }
}

/* Tablets */
@media (min-width: 481px) and (max-width: 768px) {
  .booking-section {
    width: 90%;
    font-size: 15px;
  }

  .section-heading {
    font-size: 20px;
  }

  .slot-grid {
    gap: 12px;
  }

  .slot-box {
    font-size: 16px;
  }
}

/* Laptops */
@media (min-width: 769px) and (max-width: 1366px) {
  .booking-section {
    width: 75%;
    font-size: 16px;
  }

  .section-heading {
    font-size: 24px;
  }

  .slot-box {
    font-size: 18px;
  }
}

/* Large screens */
@media (min-width: 1367px) {
  .booking-section {
    width: 60%;
    max-width: 1000px;
    font-size: 17px;
  }

  .section-heading {
    font-size: 28px;
  }

  .slot-box {
    font-size: 20px;
  }

  .legend-badge {
    font-size: 15px;
  }
}
