/* === Cancel Booking Page (Glass Style, Readable Text) === */
.cb-container {
  padding: 20px;
  padding-top: 60px;
  font-family: Arial, sans-serif;
  text-align: center;

  /* Glass effect */
  background: rgba(255, 255, 255, 0.15);
  /* backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px); */
  border-radius: 15px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.25);
  color: #111; /* darker text for readability */
  transition: all 0.3s ease;
}

/* Title */
.cb-title {
  margin-top: 36px;
  font-size: 22px;
  font-weight: bold;
  color: #004c99; /* dark blue for readability */
  text-shadow: 0 1px 3px rgba(255, 255, 255, 0.3);
}

/* Message for no bookings */
.cb-no-bookings {
  margin-top: 20px;
  font-size: 18px;
  color: #333; /* darker gray text */
}

/* === Booking List === */
.cb-list {
  margin-top: 20px;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

/* Individual booking item */
.cb-item {
  display: flex;
  justify-content: space-between;
  align-items: center;

  /* Glass effect */
  background: rgba(255, 255, 255, 0.25);
  /* backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px); */
  border-radius: 12px;
  padding: 12px 16px;
  margin-bottom: 12px;
  box-shadow: 0 4px 20px rgba(0,0,0,0.2);
  transition: all 0.3s ease;
}

.cb-item:hover {
  background: rgba(0, 102, 204, 0.15);
  color: #fff;
}

.cb-item-text {
  font-size: 15px;
  color: #111; /* dark text for readability */
  text-shadow: 0 1px 2px rgba(255,255,255,0.3);
}

/* === Cancel Button === */
.cb-cancel-booking-btn {
  background: rgba(231, 76, 60, 0.85);
  color: white;
  border: none;
  padding: 6px 14px;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cb-cancel-booking-btn:hover {
  background: rgba(192, 57, 43, 0.85);
  transform: scale(1.05);
}

/* === Confirmation Box === */
.cb-confirm-box {
  margin-top: 10px;
  text-align: center;
}

.cb-confirm-btn,
.cb-cancel-btn {
  padding: 6px 14px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  margin: 0 5px;
  transition: all 0.3s ease;
}

.cb-confirm-btn {
  background: rgba(40, 167, 69, 0.85);
  color: white;
}

.cb-confirm-btn:hover {
  background: rgba(33, 136, 56, 0.85);
  transform: scale(1.05);
}

.cb-cancel-btn {
  background: rgba(108, 117, 125, 0.85);
  color: white;
}

.cb-cancel-btn:hover {
  background: rgba(90, 98, 104, 0.85);
  transform: scale(1.05);
}

/* Responsive */
@media (max-width: 480px) {
  .cb-title { font-size: 18px; }
  .cb-no-bookings { font-size: 16px; }
  .cb-list { max-width: 90%; }
  .cb-item { flex-direction: column; text-align: center; gap: 10px; }
  .cb-cancel-booking-btn { width: 100%; padding: 10px; }
}

@media (min-width: 481px) and (max-width: 768px) {
  .cb-title { font-size: 20px; }
  .cb-list { max-width: 80%; }
}

@media (min-width: 769px) and (max-width: 1366px) {
  .cb-title { font-size: 22px; }
}
