/* ==============================
   General Reports Container
   ============================== */
   .reports-container {
    padding: 20px;
    margin-top: 60px;
    min-height: 100vh;
    font-family: "Poppins", sans-serif;
    background: #f0f4f8; /* subtle light background */
    color: #004c99;
  }
  
  /* Page Title */
  .page-title {
    text-align: center;
    font-size: 28px;
    font-weight: 700;
    margin-bottom: 25px;
    color: #004c99;
    text-shadow: 0 0 4px rgba(0, 76, 153, 0.4);
  }
  
  /* ==============================
     Filter Section (Glassmorphic + Glow)
     ============================== */
  .filter-section {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 30px;
    flex-wrap: wrap;
    background: rgba(255, 255, 255, 0.7);
    /* backdrop-filter: blur(12px); */
    border-radius: 15px;
    padding: 15px;
    border: 1px solid rgba(0, 76, 153, 0.2);
    box-shadow: 0 0 15px rgba(0, 123, 255, 0.3); /* glow effect */
  }
  
  /* Inputs */
  .filter-section input[type="date"] {
    padding: 8px 12px;
    border-radius: 8px;
    border: 1px solid rgba(0, 76, 153, 0.3);
    background: rgba(255, 255, 255, 0.9);
    color: #004c99;
  }
  
  /* Buttons */
  .filter-section button {
    padding: 8px 15px;
    border: none;
    border-radius: 10px;
    background: linear-gradient(135deg, #004c99, #007bff);
    color: white;
    font-weight: bold;
    cursor: pointer;
    transition: 0.3s;
    box-shadow: 0 0 10px rgba(0, 123, 255, 0.4); /* subtle glow */
  }
  
  .filter-section button:hover {
    transform: translateY(-2px);
    box-shadow: 0 0 15px rgba(0, 123, 255, 0.6);
  }
  
  /* ==============================
     Summary Cards (Glassmorphic + Glow)
     ============================== */
  .summary-cards {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 30px;
  }
  
  .card {
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(12px);
    border-radius: 15px;
    padding: 20px;
    width: 220px;
    text-align: center;
    box-shadow: 0 0 15px rgba(0, 123, 255, 0.2); /* glow effect */
    transition: 0.3s;
  }
  
  .card:hover {
    transform: translateY(-2px);
    box-shadow: 0 0 20px rgba(0, 123, 255, 0.4);
  }
  
  .card h3 {
    margin-bottom: 10px;
    font-size: 18px;
    color: #004c99;
  }
  
  .card p {
    font-size: 22px;
    font-weight: bold;
    color: #004c99;
  }
  
  /* ==============================
     Charts Section (Glassmorphic + Glow + Centered)
     ============================== */
  .charts {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center; /* center charts horizontally on big devices */
  }
  
  .chart-wrapper {
    width: 100%;
    overflow-x: auto;
    border-radius: 15px;
    padding-bottom: 5px;
  }
  
  .chart {
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(12px);
    border-radius: 15px;
    padding: 20px;
    min-width: 320px;
    width: max-content;
    flex-shrink: 0;
    box-shadow: 0 0 15px rgba(0, 123, 255, 0.2); /* glow effect */
    transition: 0.3s;
  }
  
  .chart h3 {
    margin-bottom: 15px;
    font-size: 18px;
    color: #004c99;
  }
  
  /* ==============================
     Reports Table (Glassmorphic + Glow)
     ============================== */
  .reports-table {
    width: 90%;
    margin: auto;
    border-collapse: collapse;
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(12px);
    border-radius: 15px;
    overflow: hidden;
    border: 1px solid rgba(0, 76, 153, 0.2);
    box-shadow: 0 0 15px rgba(0, 123, 255, 0.2); /* glow effect */
    margin-bottom: 30px;
  }
  
  .reports-table th,
  .reports-table td {
    border: 1px solid rgba(0,76,153,0.2);
    padding: 12px;
    text-align: center;
    color: #004c99;
  }
  
  .reports-table th {
    background: rgba(0, 76, 153, 0.2);
    font-weight: bold;
  }
  
  .actions-col {
    width: 150px;
  }
  
  /* View Button */
  .view-btn {
    background: linear-gradient(135deg, #004c99, #007bff);
    color: white;
    border: none;
    padding: 6px 12px;
    border-radius: 10px;
    cursor: pointer;
    font-weight: 500;
    transition: 0.3s;
    box-shadow: 0 0 8px rgba(0,123,255,0.3); /* glow */
  }
  
  .view-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 0 15px rgba(0,123,255,0.5);
  }
  
  /* ==============================
     Responsive Design
     ============================== */
  
  /* Mobile (<768px) */
  @media screen and (max-width: 767px) {
    .summary-cards, .charts {
      flex-direction: column;
      align-items: center;
    }
  
    .card {
      width: 95%;
    }
  
    .filter-section {
      flex-direction: column;
      align-items: center;
    }
  
    .filter-section input[type="date"],
    .filter-section button {
      width: 90%;
      margin-bottom: 10px;
    }
  
    .chart {
      min-width: 400px;
    }
  
    .reports-table {
      width: 100%;
      display: block;
      overflow-x: auto;
      white-space: nowrap;
    }
  
    .reports-table th,
    .reports-table td {
      padding: 10px;
      font-size: 14px;
    }
  
    .page-title {
      font-size: 22px;
    }
  }
  
  /* Tablet (768px - 991px) */
  @media screen and (min-width: 768px) and (max-width: 991px) {
    .summary-cards {
      justify-content: center;
    }
  
    .card {
      width: 48%;
    }
  
    .charts {
      align-items: center; /* center charts */
      
    }
  
    .chart {
      min-width: 400px;
    }
  
    .reports-table {
      width: 95%;
    }
  
    .reports-table th, .reports-table td {
      padding: 12px;
      font-size: 15px;
    }
  
    .page-title {
      font-size: 24px;
    }
  }
  
  /* Desktop / Large Screens (992px+) */
  @media screen and (min-width: 992px) {
    .summary-cards, .charts {
      flex-wrap: nowrap;
      justify-content: center; /* center charts/cards */
    }
  
    .card {
      width: 220px;
    }
  
    .chart {
      width: 400px;
      min-width: 400px;
    }
  
    .reports-table {
      width: 90%;
    }
  
    .reports-table th, .reports-table td {
      padding: 14px;
      font-size: 16px;
    }
  
    .page-title {
      font-size: 28px;
    }
  }
  