/* ==============================
   General Bookings Container
   ============================== */
   .bookings-container {
    padding: 20px;
    margin-top: 60px;
    font-family: "Poppins", sans-serif;
    background: #f0f4f8;
    min-height: 100vh;
    color: #004c99;
  }
  
  /* Page Title */
  .page-title {
    text-align: center;
    font-size: 28px;
    font-weight: 700;
    margin-bottom: 25px;
    color: #004c99;
    text-shadow: 0 0 4px rgba(0, 76, 153, 0.4);
  }
  
  /* ==============================
     Bookings Table (Glass + Glow)
     ============================== */
  .bookings-table {
    width: 100%;
    border-collapse: collapse;
    background: rgba(255, 255, 255, 0.7);
    /* backdrop-filter: blur(12px); */
    border-radius: 15px;
    overflow: hidden;
    border: 1px solid rgba(0, 76, 153, 0.2);
    box-shadow: 0 0 15px rgba(0, 123, 255, 0.2); /* glow effect */
    margin-bottom: 20px;
  }
  
  .bookings-table th,
  .bookings-table td {
    padding: 12px;
    border: 1px solid rgba(0, 76, 153, 0.2);
    text-align: center;
    color: #004c99;
  }
  
  .bookings-table th {
    background: rgb(0, 77, 153);
    font-weight: bold;
    color: #ffffff;
  }
  
  /* Buttons */
  .complete-btn {
    background: linear-gradient(135deg, #2ecc71, #27ae60);
    color: white;
    border: none;
    padding: 8px 14px;
    border-radius: 10px;
    margin-right: 8px;
    cursor: pointer;
    font-weight: 500;
    transition: 0.3s;
    box-shadow: 0 0 8px rgba(46, 204, 113, 0.3);
  }
  
  .complete-btn:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(46, 204, 113, 0.5);
  }
  
  .cancel-btn {
    background: linear-gradient(135deg, #e74c3c, #ff6b6b);
    color: white;
    border: none;
    padding: 8px 14px;
    border-radius: 10px;
    cursor: pointer;
    font-weight: 500;
    transition: 0.3s;
    box-shadow: 0 0 8px rgba(231, 76, 60, 0.3);
  }
  
  .cancel-btn:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(231, 76, 60, 0.5);
  }
  
  /* Status */
  .status-active {
    color: #3498db;
    font-weight: bold;
  }
  
  .status-completed {
    color: #2ecc71;
    font-weight: bold;
  }
  
  .status-cancelled {
    color: #e74c3c;
    font-weight: bold;
  }
  
  /* No bookings message */
  .no-bookings {
    text-align: center;
    padding: 20px;
    color: #777;
  }
  
  /* ==============================
     Responsive Table for Mobile
     ============================== */
  
  /* Extra Small Mobile (~220px+) */
  @media screen and (max-width: 320px) {
    .bookings-table {
      backdrop-filter: blur(6px);
      background: rgba(255,255,255,0.85);
      box-shadow: 0 0 10px rgba(0,123,255,0.15);
    }
  
    .complete-btn, .cancel-btn {
      padding: 5px 8px;
      font-size: 12px;
    }
  }
  
  /* Mobile (<768px) */
  @media screen and (max-width: 767px) {
    .bookings-table {
      display: block;
      overflow-x: auto;
      white-space: nowrap;
    }
  
    .bookings-table th,
    .bookings-table td {
      padding: 10px;
      font-size: 14px;
    }
  
    .actions-col {
      width: auto;
    }
  
    .complete-btn,
    .cancel-btn {
      margin-bottom: 5px;
    }
  
    .page-title {
      font-size: 22px;
    }
  }
  
  /* Tablet (768px - 991px) */
  @media screen and (min-width: 768px) and (max-width: 991px) {
    .bookings-table th, .bookings-table td {
      padding: 12px;
      font-size: 15px;
    }
  
    .complete-btn, .cancel-btn {
      padding: 6px 12px;
      font-size: 13px;
    }
  
    .page-title {
      font-size: 24px;
    }
  }
  
  /* Desktop / Large Screens (992px+) */
  @media screen and (min-width: 992px) {
    .bookings-table th, .bookings-table td {
      padding: 14px;
      font-size: 16px;
    }
  
    .complete-btn, .cancel-btn {
      padding: 8px 14px;
      font-size: 14px;
    }
  
    .page-title {
      font-size: 28px;
    }
  }
  