/* ==============================
   General Page Container
   ============================== */
   .slots-container {
    padding: 20px;
    margin-top: 60px;
    text-align: center;
    font-family: "Poppins", sans-serif;
    background: #ffffff; /* main page background */
    color: #004c99;
    min-height: 100vh;
    max-width: 1200px;
  }
  
  /* ==============================
     Page Title
     ============================== */
  .page-title {
    font-size: 28px;
    font-weight: 600;
    margin-bottom: 25px;
    color: #004c99;
  }
  
  /* ==============================
     Add Slot Form (Glassmorphic White)
     ============================== */
  .add-slot {
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(12px);
    border: 1px solid rgba(0, 76, 153, 0.2);
    border-radius: 15px;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
    width: 90%;
    max-width: 600px;
    margin: 20px auto;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
    justify-content: center;
    padding: 20px;
    box-sizing: border-box;
  }
  
  .add-slot input {
    padding: 10px;
    flex: 1 1 100%;
    min-width: 120px;
    max-width: 280px;
    border: 1px solid rgba(0, 76, 153, 0.3);
    border-radius: 8px;
    background: rgba(255, 255, 255, 1);
    color: #004c99;
    font-weight: 500;
    text-align: center;
  }
  
  .add-slot input::placeholder {
    color: rgba(0, 76, 153, 0.6);
  }
  
  .add-slot button {
    padding: 10px 20px;
    cursor: pointer;
    background: linear-gradient(135deg, #004c99, #007bff);
    color: white;
    font-weight: 600;
    border: none;
    border-radius: 10px;
    transition: 0.3s ease;
    flex: 1 1 100%;
    max-width: 200px;
    text-align: center;
  }
  
  .add-slot button:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 10px rgba(0, 76, 153, 0.4);
  }
  
  /* ==============================
     Slots Table (Glassmorphic Blue Gradient)
     ============================== */
     
  .slots-table {
    width: 95%;
    max-width: 900px;
    margin: 20px auto;
    border-collapse: collapse;
    background: rgba(255, 255, 255, 0.7); /* subtle blue tint */
    /* backdrop-filter: blur(8px); */
    border-radius: 15px;
    overflow: hidden;
    border: 1px solid rgba(0, 76, 153, 0.2);
    box-shadow: 0 0 15px rgba(0, 123, 255, 0.2);
    text-align: center;
    transition: 0.3s;
  }
  
  .slots-table th,
  .slots-table td {
    padding: 14px;
    color: #004c99; /* high contrast text */
    font-weight: 500;
    border: 1px solid rgba(0, 123, 255, 0.281);
    word-break: break-word;
    background:rgba(255, 255, 255, 0.856); 
  }
  
  .slots-table th {
    
    background: rgb(0, 77, 153);
    color: #ffffff;
    font-weight: bold;
    padding: 14px;
    text-shadow: 0 0 4px rgba(255,255,255,0.5);
  }
  
  .actions-col {
    text-align: center;
  }
  
  /* ==============================
     Buttons inside Table
     ============================== */
  .toggle-btn,
  .delete-btn {
    border: none;
    padding: 8px 14px;
    border-radius: 8px;
    cursor: pointer;
    color: white;
    font-weight: 500;
    transition: 0.3s ease;
    margin: 2px 2px;
  }
  
  .toggle-btn {
    background: linear-gradient(135deg, #004c99, #007bff);
  }
  
  .delete-btn {
    background: linear-gradient(135deg, #e74c3c, #ff6b6b);
  }
  
  .toggle-btn:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 8px rgba(0, 76, 153, 0.3);
  }
  
  .delete-btn:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 8px rgba(231, 76, 60, 0.3);
  }
  
  /* Status Colors */
  .status-available {
    color: #2ecc71;
    font-weight: bold;
  }
  
  .status-occupied {
    color: #e74c3c;
    font-weight: bold;
  }
  
  /* ==============================
     Responsive Styles
     ============================== */
  
  /* Extra Small Mobile (~220px+) */
  @media (max-width: 320px) {
    .add-slot {
      width: 95%;
      padding: 10px;
    }
    .add-slot input,
    .add-slot button {
      max-width: 100%;
      flex: 1 1 100%;
    }
    .slots-table {
      width: 100%;
      font-size: 12px;
    }
  }
  
  /* Mobile (321px - 767px) */
  @media (min-width: 321px) and (max-width: 767px) {
    .add-slot {
      flex-direction: column;
      gap: 8px;
      width: 90%;
    }
    .slots-table {
      width: 100%;
      display: block;
      overflow-x: auto;
      white-space: nowrap;
    }
    .page-title {
      font-size: 22px;
    }
  }
  
  /* Tablet (768px - 991px) */
  @media (min-width: 768px) and (max-width: 991px) {
    .add-slot input {
      flex: 1 1 45%;
      max-width: 220px;
    }
    .add-slot button {
      flex: 1 1 100%;
      max-width: 180px;
    }
    .slots-table {
      width: 95%;
    }
  }
  
  /* Desktop (992px+) */
  @media (min-width: 992px) {
    .add-slot input {
      flex: 1 1 45%;
      max-width: 280px;
    }
    .add-slot button {
      max-width: 200px;
    }
    .slots-table {
      width: 80%;
    }
  }
  