/* Booking History Page */
.booking-history-page {
  padding: 20px;
  text-align: center;
  margin-top: 60px;
  min-height: calc(100vh - 60px);
  background: rgba(255, 255, 255, 0.15);
  border-radius: 12px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
  color: #333;
  transition: all 0.3s ease;
}

/* Heading */
.welcome {
  font-size: 22px;
  margin-bottom: 20px;
  color: #004c99;
  text-shadow: 0 0 3px rgba(0,0,0,0.2);
}

/* Table container */
.table-container {
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  margin: auto;
}

/* Booking History Table styled like Users Table */
.history-table {
    width: 80%;
    max-width: 900px;
    margin: 20px auto;
    border-collapse: collapse;
    background: rgba(255, 255, 255, 0.7);
    border-radius: 15px;
    overflow: hidden;
    border: 1px solid rgba(0, 76, 153, 0.2);
    box-shadow: 0 0 15px rgba(0, 123, 255, 0.2);
    text-align: center;
    transition: 0.3s;
}

.history-table:hover {
    box-shadow: 0 0 30px rgba(0, 123, 255, 0.4);
}

.history-table th {
    background: rgb(0, 77, 153);
    color: #ffffff;
    font-weight: bold;
    padding: 14px;
    text-shadow: 0 0 4px rgba(255,255,255,0.5);
}

.history-table td {
    padding: 14px;
    color: #004c99;
    font-weight: 500;
    border: 1px solid rgba(0, 76, 153, 0.2);
    text-align: center;
    word-break: break-word;
    transition: 0.3s;
}

.history-table td:hover {
    background: rgba(217, 243, 247, 0.95);
    box-shadow: 0 0 10px rgba(0,123,255,0.2);
}

/* Status colors */
.booked {
    color: green;
    font-weight: bold;
}

.completed {
    color: blue;
    font-weight: bold;
}

.cancelled {
    color: red;
    font-weight: bold;
}

/* Responsive adjustments */
@media (min-width: 769px) and(max-width: 1024px) {
    .history-table {
        min-width: 600px;
        font-size: 14px;
    }
}

@media (min-width: 481px) and (max-width: 768px) {
    .history-table {
        min-width: 500px;
        font-size: 13px;
    }
}

@media (max-width: 480px) {
    .history-table {
        min-width: 400px;
        font-size: 12px;
    }
}
