/* =========================
   Base Message Styles
   ========================= */
.message {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 12px 20px;
  border-radius: 12px;
  color: #fff;
  font-weight: 600;
  z-index: 9999;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
  opacity: 0.95;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 250px;
  max-width: 90%;
  text-align: center;
  /* backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px); */
  border: 1px solid rgba(255, 255, 255, 0.25);
  background: rgba(255, 255, 255, 0.1);
  animation: slideDown 0.5s ease forwards;
  transition: transform 0.4s ease, opacity 0.4s ease;
}

/* =========================
   Message Types
   ========================= */
.message.success {
  background: linear-gradient(135deg, rgba(42, 247, 0, 0.993), rgb(1, 145, 20));
  box-shadow: 0 0 15px rgba(11, 119, 1, 0.986);
}

.message.error {
  background: linear-gradient(135deg, rgba(255, 0, 0, 0.959), rgb(167, 5, 2));
  box-shadow: 0 0 15px rgba(211, 79, 79, 0.6);
}

.message.info {
  background: linear-gradient(135deg, rgb(2, 137, 247), rgba(11, 102, 221, 0.986));
  box-shadow: 0 0 15px rgba(28, 47, 218, 0.993);
}

/* =========================
   Children Container (for buttons)
   ========================= */
.message-children {
  margin-top: 10px;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: center;
}

/* Buttons inside message */
.message-children button {
  padding: 6px 12px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.2s ease;
  backdrop-filter: blur(10px);
}

.message-children button.confirm {
  background-color: rgba(40, 167, 69, 0.8);
  color: white;
}

.message-children button.cancel {
  background: linear-gradient(135deg, rgba(255, 0, 0, 0.959), rgb(167, 5, 2));
  color: white;
}

.message-children button:hover {
  transform: scale(1.05);
  opacity: 0.9;
}

/* =========================
   Animations
   ========================= */
@keyframes slideDown {
  0% {
    transform: translate(-50%, -50px);
    opacity: 0;
  }
  100% {
    transform: translate(-50%, 0);
    opacity: 0.95;
  }
}

@keyframes slideUp {
  0% {
    transform: translate(-50%, 0);
    opacity: 0.95;
  }
  100% {
    transform: translate(-50%, -50px);
    opacity: 0;
  }
}

/* Hide animation */
.message.hide {
  animation: slideUp 0.5s ease forwards;
}

/* =========================
   Responsive Styles
   ========================= */

/* Small devices (‚â§ 480px) */
@media (max-width: 480px) and (min-width: 320px) {
  .message {
    width: 90%;
    min-width: unset;
    padding: 10px 15px;
    font-size: 14px;
  }

  .message-children button {
    padding: 5px 10px;
    font-size: 14px;
  }
}

/* Very small devices (‚â§ 320px) */
@media (max-width: 320px) {
  .message {
    padding: 8px 10px;
    font-size: 12px;
  }

  .message-children button {
    padding: 4px 8px;
    font-size: 12px;
  }
}
