* {
  font-family: "Trebuchet MS", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.form-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 15px;
  overflow: hidden;
}

/* üîÆ Blurred Gradient Background */
.form-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #00c6ff, #0072ff, #cc10a0);
  background-size: 300% 300%;
  animation: gradientMove 8s ease infinite;
  filter: blur(10px);
  z-index: 0;
}

/* üåà Moving Gradient Animation */
@keyframes gradientMove {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* üí† Glassmorphic Login Form */
.form-design {
  position: relative;
  z-index: 1;
  background: rgba(255, 255, 255, 0.15);
  /* backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px); */
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 16px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
  padding: 40px;
  width: 100%;
  max-width: 400px;
  color: #fff;
}

/* üßä Header */
.form-design h1 {
  text-align: center;
  margin-bottom: 25px;
  color: #ffffff;
  font-size: 28px;
  font-weight: 700;
}

/* üè∑Ô∏è Labels */
.form-design label {
  display: block;
  text-align: left;
  margin: 8px 0 5px;
  font-weight: 600;
  color: #f0f0f0;
  font-size: 16px;
}

/* üß© Inputs */
.form-design input {
  width: 100%;
  padding: 12px;
  margin-bottom: 20px;
  border: none;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.3);
  color: #fff;
  font-size: 16px;
  outline: none;
  transition: all 0.3s ease;
}

.form-design input::placeholder {
  color: #f0f0f0;
}

.form-design input:focus {
  background: rgba(255, 255, 255, 0.5);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
}

/* üîò Button */
.form-design button {
  width: 100%;
  padding: 12px;
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  border: none;
  border-radius: 8px;
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
}

.form-design button:hover {
  transform: scale(1.05);
  box-shadow: 0 0 15px rgba(37, 117, 252, 0.6);
}

/* üîó Footer Links */
.form-footer {
  text-align: left;
  padding-top: 10px;
  font-size: 16px;
  color: #e0e0e0;
}

.link-text {
  color: #fff;
  cursor: pointer;
  font-weight: bold;
  text-decoration: underline;
}

.admin-text {
  color: #e0e0e0;
  text-decoration: none;
  font-size: 16px;
}

/* ================== */
/* üì± Responsiveness */
/* ================== */

/* Small Mobile (‚â§ 320px) */
@media (max-width: 320px) {
  .form-design {
    padding: 15px;
    max-width: 95%;
  }
  .form-design h1 {
    font-size: 18px;
  }
  .form-design input,
  .form-design button,
  .form-design label {
    font-size: 12px;
  }
  .form-footer {
    font-size: 12px;
  }
  .admin-text {
    font-size: 12px;
  }
}

/* Mobile Phones (‚â§ 480px) */
@media (max-width: 480px) and (min-width: 320px) {
  .form-design {
    padding: 25px;
    max-width: 85%;
  }
  .form-design h1 {
    font-size: 22px;
  }
  .form-design input,
  .form-design button {
    font-size: 14px;
  }
}

/* Tablets (‚â§ 768px) */
@media (max-width: 768px) and (min-width: 480px) {
  .form-design {
    max-width: 80%;
  }
  .form-design h1 {
    font-size: 24px;
  }
}

/* Small Laptops (‚â§ 1024px) */
@media (max-width: 1024px) and (min-width: 768px) {
  .form-design {
    max-width: 60%;
  }
}

/* Large Screens (‚â• 1200px) */
@media (min-width: 1200px) {
  .form-design {
    max-width: 500px;
  }
}
