/* === Container (Glass Effect) === */
.uh-container {
  padding: 20px;
  font-family: Arial, sans-serif;
  text-align: center;
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
  box-sizing: border-box;

  background: rgba(255, 255, 255, 0.15);
  /* backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px); */
  border-radius: 15px;
  box-shadow: 0 8px 30px rgba(0,0,0,0.25);
  color: #111; /* readable text */
  transition: all 0.3s ease;
}

/* Welcome Text */
.uh-welcome {
  font-size: 26px;
  font-weight: bold;
  color: #004c99;
  margin-bottom: 10px;
  text-shadow: 0 1px 3px rgba(255,255,255,0.3);
}

/* === Section Box (Glass) === */
.uh-section {
  background: rgba(255, 255, 255, 0.15);
  /* backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px); */
  padding: 18px 25px;
  border-radius: 15px;
  width: 70%;
  max-width: 850px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  text-align: left;
  transition: all 0.3s ease;
  color: #111;
}

/* Section Titles */
.uh-title {
  font-size: 20px;
  font-weight: bold;
  color: #004c99;
  margin-bottom: 15px;
  border-left: 5px solid #007bff;
  padding-left: 10px;
  text-shadow: 0 1px 2px rgba(255,255,255,0.3);
}

/* === Cards (Summary) === */
.uh-cards {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.uh-card {
  background: rgba(255, 255, 255, 0.15);
  /* backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px); */
  padding: 15px 20px;
  border-radius: 12px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.2);
  flex: 1;
  min-width: 120px;
  max-width: 180px;
  text-align: center;
  transition: all 0.3s ease;
  color: #111;
}

.uh-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(0,0,0,0.3);
}

.uh-card h3 {
  font-size: 15px;
  margin-bottom: 6px;
  color: #333;
}

.uh-card p {
  font-size: 18px;
  font-weight: bold;
  color: #007bff;
}

/* === Legend === */
.uh-legend {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 10px;
}

.uh-legend-item {
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 13px;
  font-weight: bold;
  color: #fff;
  min-width: 70px;
  text-align: center;
  backdrop-filter: blur(5px);
}

.uh-legend-item.available { background: #28a745; }
.uh-legend-item.occupied { background: #dc3545; }
.uh-legend-item.reserved { background: #ffc107; color: #333; }
.uh-legend-item.special  { background: #17a2b8; }

/* === Parking Layout (Glass Slots) === */
.uh-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  gap: 15px;
  max-width: 600px;
  margin: 0 auto;
}

.uh-slot {
  height: 80px;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  font-weight: bold;
  font-size: 16px;
  color: white;
  transition: all 0.3s ease;
  backdrop-filter: blur(5px);
}

/* Slot colors */
.uh-slot.available { background: #5ed87a; border: 2px solid #02a302; }
.uh-slot.occupied  { background: #f37b7b; border: 2px solid #fa0000; }
.uh-slot.reserved  { background: #ffc107; color: #333; }
.uh-slot.special   { background: #17a2b8; }

/* Hover effects */
.uh-slot.available:hover { background: #00da33; transform: scale(1.05); }
.uh-slot.occupied:hover  { background: #c82333; }
.uh-slot.reserved:hover  { background: #e0a800; }
.uh-slot.special:hover   { background: #138496; }

/* === Actions === */
.uh-actions {
  margin-top: 15px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
}

.uh-actions button {
  padding: 10px 18px;
  border: none;
  border-radius: 8px;
  background: #007bff;
  color: white;
  font-size: 15px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.uh-actions button:hover {
  background: #0056b3;
  transform: scale(1.05);
}

/* === Chart === */
.uh-chart {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 250px;
}

/* === Footer === */
.uh-footer {
  margin-top: 30px;
  padding: 15px;
  background: rgba(0,0,0,0.3);
  color: white;
  width: 100%;
  text-align: center;
  backdrop-filter: blur(5px);
}

.uh-footer-container {
  max-width: 900px;
  margin: auto;
}

/* === RESPONSIVENESS === */

/* Large screens */
@media (min-width: 1200px) {
  .uh-title { font-size: 24px; }
  .uh-card p { font-size: 20px; }
  .uh-welcome { font-size: 30px; }
}

/* Tablets */
@media (max-width: 992px) {
  .uh-section { width: 85%; }
  .uh-title { font-size: 19px; }
  .uh-slot { height: 70px; width: 70px; font-size: 14px; }
}

/* Mobiles */
@media (max-width: 576px) {
  .uh-section { width: 95%; padding: 12px 15px; }
  .uh-title { font-size: 17px; }
  .uh-welcome { font-size: 22px; }
  .uh-card h3 { font-size: 13px; }
  .uh-card p { font-size: 16px; }
  .uh-slot { height: 60px; width: 60px; font-size: 13px; }
}
