/* =========================
   üåê Sticky Glass Navbar
   ========================= */
   .navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 60px;
    background: rgba(0, 102, 204, 0.9);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    z-index: 1000;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
    transition: all 0.4s ease;
  }
  
  /* üçî Hamburger button */
  .hamburger {
    font-size: 28px;
    cursor: pointer;
    transition: transform 0.3s ease, color 0.3s ease;
  }
  .hamburger:hover {
    transform: scale(1.1);
    color: #00c6ff;
  }
  
  /* üî∑ Logo */
  .logo {
    font-size: 20px;
    font-weight: bold;
    color: #ffffff;
    text-shadow: 0 0 6px rgba(255, 255, 255, 0.6);
    transition: all 0.3s ease;
  }
  
  /* üë§ Profile section */
  .profile {
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
    color: white;
    font-size: 14px;
    transition: all 0.3s ease;
  }
  .profile a {
    text-decoration: none;
    font-size: 18px;
    color: #ffffff;
    transition: color 0.3s ease;
  }
  .profile a:hover {
    color: #00c6ff;
  }
  .profile img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
    margin-left: 8px;
    border: 2px solid rgba(255, 255, 255, 0.5);
    transition: transform 0.3s ease, border-color 0.3s ease;
  }
  .profile img:hover {
    transform: scale(1.1);
    border-color: #00c6ff;
  }
  
  /* =========================
     üßä Sidebar (Glass Effect)
     ========================= */
  .sidebar {
    position: fixed;
    top: 0;
    left: -300px;
    width: 250px;
    height: 100%;
    background: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    box-shadow: 4px 0 25px rgba(0, 0, 0, 0.25);
    padding: 20px;
    transition: left 0.4s cubic-bezier(0.77,0,0.175,1);
    z-index: 1100;
    border-right: 1px solid rgba(255, 255, 255, 0.2);
  }
  .sidebar.open {
    left: 0;
  }
  
  /* ‚ùå Close Button */
  .sidebar .close-btn {
    font-size: 28px;
    border: none;
    background: none;
    cursor: pointer;
    color: #ffffff;
    text-shadow: 0 0 6px rgba(0,0,0,0.5);
    transition: transform 0.3s ease, color 0.3s ease;
    float: right;
  }
  .sidebar .close-btn:hover {
    color: #ff4c4c;
    transform: rotate(90deg) scale(1.1);
  }
  
  /* üìã Sidebar Menu */
  .sidebar ul {
    list-style: none;
    padding: 60px 0 0;
    transition: all 0.3s ease;
  }
  .sidebar ul li {
    margin: 18px 0;
  }
  .sidebar ul li a {
    text-decoration: none;
    font-size: 18px;
    color: #004c99;
    font-weight: 600;
    transition: all 0.3s ease;
    display: block;
    padding: 8px 10px;
    border-radius: 8px;
  }
  .sidebar ul li a:hover {
    background: rgba(0, 102, 204, 0.2);
    color: #00c6ff;
    transform: translateX(6px);
  }
  
  /* =========================
     üî≤ Overlay (Dimmed Background)
     ========================= */
  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(5px);
    z-index: 1050;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease, visibility 0.3s ease;
    pointer-events: none;
  }
  
  /* Show overlay */
  .overlay.active {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
  }
  
  /* Prevent scrolling when overlay is active */
  body.overlay-active {
    overflow: hidden;
  }
  
  /* =========================
     üì± RESPONSIVENESS
     ========================= */
  
  /* Small devices (phones, <576px) */
  @media (max-width: 575px) {
    .navbar { padding: 0 10px; }
    .logo { font-size: 16px; }
    .profile a { font-size: 14px; }
    .profile img { width: 30px; height: 30px; }
    .sidebar { width: 200px; }
  }
  
  /* Medium devices (tablets, 576px‚Äì768px) */
  @media (min-width: 576px) and (max-width: 768px) {
    .logo { font-size: 18px; }
    .profile a { font-size: 16px; }
    .profile img { width: 35px; height: 35px; }
    .sidebar { width: 220px; }
  }
  
  /* Large devices (laptops, 769px‚Äì1200px) */
  @media (min-width: 769px) and (max-width: 1200px) {
    .navbar { padding: 0 30px; }
    .logo { font-size: 22px; }
    .profile a { font-size: 18px; }
    .sidebar { width: 250px; }
  }
  
  /* Extra large devices (desktop & big screens, >1200px) */
  @media (min-width: 1201px) {
    .navbar { padding: 0 50px; }
    .logo { font-size: 24px; }
    .profile a { font-size: 20px; }
    .profile img { width: 45px; height: 45px; }
    .sidebar { width: 280px; }
  }
  