/* ==============================
   General Users Container
   ============================== */
   .users-container {
    padding: 20px;
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Poppins", sans-serif;
    background: #ffffff;
    color: #004c99;
    min-height: 100vh;
    max-width: 1200px;
}

/* ==============================
   Page Title
   ============================== */
.page-title {
    text-align: center;
    font-size: 28px;
    font-weight: 600;
    margin-bottom: 25px;
    color: #004c99;
    text-shadow: 0 0 6px rgba(0, 76, 153, 0.4); /* glow */
}

/* ==============================
   Total Users Box (Glassmorphic + Glow)
   ============================== */
.total-users {
    font-weight: bold;
    font-size: 18px;
    text-align: center;
    margin-bottom: 20px;
    background: rgba(255, 255, 255, 0.8);
    /* backdrop-filter: blur(12px); */
    border: 1px solid rgba(0, 76, 153, 0.2);
    border-radius: 15px;
    box-shadow: 0 0 20px rgba(0, 123, 255, 0.3); /* glow */
    width: 80%;
    color: #004c99;
    padding: 20px;
    transition: 0.3s;
}

.total-users:hover {
    box-shadow: 0 0 25px rgba(0, 123, 255, 0.5);
}

/* ==============================
   Users Table (Glassmorphic + Glow)
   ============================== */
.users-table {
    width: 80%;
    max-width: 900px;
    margin: 20px auto;
    border-collapse: collapse;
    background: rgba(255, 255, 255, 0.7);
    /* backdrop-filter: blur(12px); */
    border-radius: 15px;
    overflow: hidden;

    border: 1px solid rgba(0, 76, 153, 0.2);
    box-shadow: 0 0 15px rgba(0, 123, 255, 0.2); /* glow effect */
    margin-bottom: 20px;
    text-align: center;
    transition: 0.3s;
}


.users-table:hover {
  box-shadow: 0 0 30px rgba(0, 123, 255, 0.4);
}

/* Table header */
.users-table th {
  background: rgb(0, 77, 153);
  color: #ffffff;
  font-weight: bold;
  padding: 14px;
  text-shadow: 0 0 4px rgba(255,255,255,0.5);
}

/* Table rows */
.users-table td {
    padding: 14px;
    color: #004c99;
    /* background: rgba(217, 243, 247, 0.856);  */
    font-weight: 500;
    border: 1px solid rgba(0, 76, 153, 0.2);
    text-align: center;
    word-break: break-word;
    transition: 0.3s;
  }
  
.users-table td:hover {
    background: rgba(217, 243, 247, 0.95);
    box-shadow: 0 0 10px rgba(0,123,255,0.2);
}

.actions-col {
    text-align: center;
}

/* ==============================
   Buttons inside Table (Glow)
   ============================== */
.delete-btn {
    background: linear-gradient(135deg, #e74c3c, #ff6b6b);
    color: white;
    border: none;
    padding: 8px 14px;
    border-radius: 10px;
    cursor: pointer;
    font-weight: 500;
    transition: 0.3s ease;
    margin: 2px 2px;
    box-shadow: 0 0 8px rgba(231,76,60,0.3);
}

.delete-btn:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(231,76,60,0.6);
}

/* No users message */
.no-users {
    text-align: center;
    padding: 20px;
    color: #777;
}

/* ==============================
   Responsive Styles
   ============================== */

/* Extra Small Mobile (~220px+) */
@media (max-width: 320px) {
    .total-users {
        width: 95%;
        font-size: 14px;
        padding: 15px;
    }
    .users-table {
        width: 100%;
        font-size: 12px;
    }
    .delete-btn {
        padding: 5px 8px;
        font-size: 12px;
    }
}

/* Mobile (321px - 767px) */
@media (min-width: 321px) and (max-width: 767px) {
    .total-users {
        width: 90%;
        font-size: 16px;
        padding: 15px;
    }
    .users-table {
        width: 100%;
        display: block;
        overflow-x: auto;
        white-space: nowrap;
    }
    .page-title {
        font-size: 22px;
    }
    .delete-btn {
        padding: 6px 10px;
        font-size: 13px;
    }
}

/* Tablet (768px - 991px) */
@media (min-width: 768px) and (max-width: 991px) {
    .total-users {
        width: 90%;
        font-size: 17px;
        padding: 18px;
    }
    .users-table {
        width: 95%;
    }
    .users-table th, .users-table td {
        padding: 12px;
        font-size: 15px;
    }
    .delete-btn {
        padding: 6px 10px;
        font-size: 13px;
    }
    .page-title {
        font-size: 24px;
    }
}

/* Desktop / Large Screens (992px+) */
@media (min-width: 992px) {
    .total-users {
        width: 80%;
        font-size: 18px;
        padding: 20px;
    }
    .users-table {
        width: 80%;
    }
    .users-table th, .users-table td {
        padding: 14px;
        font-size: 16px;
    }
    .delete-btn {
        padding: 8px 14px;
        font-size: 14px;
    }
    .page-title {
        font-size: 28px;
    }
}
