/* === Profile Page Container (Glass Effect) === */
.profile-page {
  max-width: 500px;
  margin: 80px auto 40px;
  padding: 20px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.15);
  /* backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px); */
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.25);
  box-sizing: border-box;
  color: #111; /* readable text */
  transition: all 0.3s ease;
}

/* === Heading === */
.heading {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
  color: #004c99;
  font-size: 24px;
  font-weight: bold;
  text-shadow: 0 1px 2px rgba(255,255,255,0.3);
}

/* === Profile Details === */
.profile-details p {
  font-size: 16px;
  margin: 10px 0;
  word-wrap: break-word;
}

/* === Button Styles (Glass / Smooth) === */
.edit-btn,
.save-btn,
.cancel-btn,
.logout-btn {
  padding: 10px 15px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  font-size: 14px;
  transition: all 0.3s ease;
  /* backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px); */
  color: white;
}

/* Edit Button */
.edit-btn {
  background: rgba(0, 123, 255, 0.8);
}
.edit-btn:hover {
  background: rgba(0, 86, 179, 0.85);
  transform: scale(1.05);
}

/* Save Button */
.save-btn {
  background: rgba(40, 167, 69, 0.85);
  margin-right: 10px;
}
.save-btn:hover {
  background: rgba(30, 126, 52, 0.9);
  transform: scale(1.05);
}

/* Cancel Button */
.cancel-btn {
  background: rgba(220, 53, 69, 0.85);
}
.cancel-btn:hover {
  background: rgba(167, 29, 42, 0.9);
  transform: scale(1.05);
}

/* Logout Button */
.logout-btn {
  background: rgba(220, 53, 69, 0.85);
}
.logout-btn:hover {
  background: rgba(167, 29, 42, 0.9);
  transform: scale(1.05);
}

/* === Button Row (Flex) === */
.button-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  transition: all 0.3s ease;
}

/* === Profile Edit Fields === */
.profile-edit label {
  display: block;
  margin-bottom: 12px;
  font-size: 14px;
}

.profile-edit input {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
  border-radius: 8px;
  border: 1px solid rgba(200,200,200,0.5);
  font-size: 14px;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(5px);
  color: #111;
  transition: all 0.3s ease;
}

.profile-edit input:focus {
  border-color: #007bff;
  outline: none;
}

/* === Responsive Design === */
@media (max-width: 768px) {
  .profile-page {
      width: 90%;
      margin: 60px auto;
      padding: 15px;
  }
  .heading { font-size: 20px; }
  .profile-details p { font-size: 15px; }

  .button-row {
      flex-direction: column;
      gap: 10px;
  }

  .edit-btn,
  .logout-btn,
  .save-btn,
  .cancel-btn {
      width: 100%;
  }
}

@media (max-width: 480px) {
  .profile-page {
      width: 95%;
      margin: 40px auto;
      padding: 12px;
  }
  .heading { font-size: 18px; }
  .profile-details p { font-size: 14px; }
}
